.fa-icon {
  width: auto;
  height: 2.5em;
  /* You would have to include the following two lines to make this work in Safari */
  max-width: 100%;
  max-height: 100%;
}
a.social,a.social:visited { color: #fff;
}
a.social:hover { color: #fff;
}
.social {
	margin: 1em 1em; width: 50px; height: 40px;
  display: inline-block; line-height: 7px; color: #fff;
	-webkit-transform:rotateY(0deg);
	        transform:rotateY(0deg);
	-webkit-transition:-webkit-transform .25s ease-out;
	transition:-webkit-transform .25s ease-out;
	transition:transform .25s ease-out;
	transition:transform .25s ease-out, -webkit-transform .25s ease-out;
	-webkit-transform-style:preserve-3d;
	        transform-style:preserve-3d;
}
.social > div {
	width: 50px; background: rgb(24, 24, 24);;
  position: absolute; top: 0; left: 0; right: 0; bottom: 0;
  border-radius: 5%;
}
.social >.front {
  -webkit-transform:translateZ(19px);
          transform:translateZ(19px);
}
.social >.back {
	background: #3B5998; font-size: 2em;
	-webkit-transform:rotateY(-100deg) translateZ(19px);
	        transform:rotateY(-100deg) translateZ(19px);
}
.social.social-github > .back { background: #f3f3f3; color: rgb(24, 24, 24);
}
.social.social-linkedin > .back { background: #4875B4;
}
.social.social-mail > .back {background: #f3f3f3; color: rgb(24, 24, 24);
}

/* Hover */
.social:hover {
	-webkit-transform: rotateY(100deg);
	        transform: rotateY(100deg);
}

.fa-icon {
  display: inline-block;
  fill: currentColor;
  overflow: visible;
}
.fa-icon > g {
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-flip-both {
  -webkit-transform: scale(-1, -1);
          transform: scale(-1, -1);
}
.fa-spin > g {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-pulse > g {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
.fa-inverse {
  color: #fff;
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
